import Vue from 'vue'
import VueRouter from 'vue-router' // 引入路由

// 引入文件
import HelloWorld from '../components/HelloWorld'
import Test from '../components/Test'

Vue.use(VueRouter)

export default new VueRouter({
  // mode: 'history',
  // 路由配置
  routes: [
    {
      // 路由的路径
      path: '/helloWorld',
      // 路由名称（可以不添加）
      name: 'helloWorld',
      // 跳转的组件
      component: HelloWorld
    },
    {
      path: '/test',
      // 路由名称（可以不添加）
      name: 'test',
      component: Test
    }
  ]
})
